import { makeObservable, makeAutoObservable, observable, computed, autorun } from "mobx"

class PayeeStore {
  payee = {};
  #payeePersistence;

  constructor(payeePersistence) {
    this.#payeePersistence = payeePersistence;
    this.payee = this.#payeePersistence.fetchData() || {};

    makeObservable(this, {
      payee: observable
    });

    autorun(() => {
      console.log("---------payeestore----")
      console.log(Object.assign({}, this.payee))
      console.log("--------------------------")

      this.#payeePersistence.saveWithDelay(this.payee);
    })
  }

}

export default PayeeStore;