
class Persistence {
  #key;
  #lastChangeInMs = Date.now();

  constructor(key) {
    this.#key = key;
  }

  fetchData() {
    return JSON.parse(localStorage.getItem(this.#key));
  }

  saveWithDelay(data) {
    if (Date.now() - this.#lastChangeInMs < 1000) {
      this.timerToPersist && clearTimeout(this.timerToPersist);
      console.log("limpou timer");
    }
    this.#lastChangeInMs = Date.now();

    const localKey = this.#key;
    this.timerToPersist = setTimeout(
      function() {
        localStorage.setItem(localKey, JSON.stringify(data));
        console.log(`${localKey} Saved on LocalStorage!`); 
      }, 
      1000
    );
  }
}

export default Persistence;