import React, { useState } from 'react';
import { observer } from "mobx-react";
import Receipt from '../components/receipt/Receipt';
import ReceiptStub from '../components/receipt/ReceiptStub';

import './ReceiptsToPrint.css';

const ReceiptsToPrint = observer((props) => {
  const receiptStore = props.receiptStore;
  const payeeStore = props.payeeStore;

  return (
    <div className="receipt-to-print-list">
      <button className="button-primary no-printable" onClick={window.print}>Imprimir</button>

      <div className="">
        <h2>Canhotos</h2>
        {Object.keys(receiptStore.receipts).map((id) => (
          <div key={id}>
            <ReceiptStub
              key={id}
              index={id}
              payee={payeeStore.payee.name}
              payeeCpfOrRg={payeeStore.payee.cpfOrRg}
              payeeAddress={payeeStore.payee.address}
              date={payeeStore.payee.date}
              receipt={receiptStore.receipts[id]}
            />
          </div>
        ))} 

        <h2>Recibos</h2>
        {Object.keys(receiptStore.receipts).map((id) => (
          <div key={id}>
            <Receipt
              key={id}
              index={id}
              payee={payeeStore.payee.name}
              payeeCpfOrRg={payeeStore.payee.cpfOrRg}
              payeeAddress={payeeStore.payee.address}
              date={payeeStore.payee.date}
              receipt={receiptStore.receipts[id]}
            />
          </div>
        ))} 
      </div>
    </div>
  );
})

export default ReceiptsToPrint;