import React, {useState} from 'react';
import './css/Normalize.css';
import './css/Skeleton.css';
import './css/App.css';


import ReceiptStore from './stores/ReceiptStore';
import PayeeStore from './stores/PayeeStore';
import { observer } from "mobx-react";

import PayeeForm from './pages/PayeeForm';
import ReceiptCards from './pages/ReceiptCards';

import Receipt from './components/receipt/Receipt';
import ReceiptsToPrint from './pages/ReceiptsToPrint';

import Persistence from './stores/Persistence';

function pathSplitted() {
  const splitted = window.location.hash.substr(1).split('/');
  return {
    first: splitted[0] || '',
    second: splitted[1] || ''
  }

}

const App = observer(() => {
  
  const [currentPage, setCurrentPage] = useState(pathSplitted().first);
  const [identifier, setIdentifier] = useState(pathSplitted().second);
  function locationHashChanged() {
    setCurrentPage(pathSplitted().first);
    setIdentifier(pathSplitted().second);
  }
  window.onhashchange = locationHashChanged;

  const receiptPersistence = new Persistence('receiptStore');
  const receiptStore = new ReceiptStore(receiptPersistence);
  
  const payeePersistence = new Persistence('payeeStore');
  const payeeStore = new PayeeStore(payeePersistence);

  const[payerNameFiltering, setPayerNameFiltering] = useState("");

  return (
    <div className="rootApp">
      <header className="top-app-bar">
        <div className="top-app-bar__left-part">Recibos Facil</div>
        <div className="top-app-bar__right-part">
          <input 
              className="u-full-width" 
              type="text"   
              name="search" 
              placeholder="Pesquise por nome" 
              id="search"
              value={payerNameFiltering} onChange={(e) => setPayerNameFiltering(e.target.value)}
              />
        </div>
      </header>
      
      
      <main>
        {
          (() => {

            if (!currentPage ||  currentPage == 'receipt-list') {
              return <ReceiptCards 
                payerNameFiltering={payerNameFiltering}
                receiptStore={receiptStore} 
                payeeStore={payeeStore}>
              </ReceiptCards>
            }

            if (currentPage == 'payee-form') {
              return  <PayeeForm payeeStore={payeeStore}></PayeeForm>
            }

            if (currentPage == 'receipt' && identifier) {
              const receipt = receiptStore.receipts[identifier];
              return <Receipt
                key={receipt.id}
                index={receipt.id}
                payee={payeeStore.payee.name}
                payeeCpfOrRg={payeeStore.payee.cpfOrRg}
                payeeAddress={payeeStore.payee.address}
                date={payeeStore.payee.date}
                receipt={receipt}
              />
            }

            if (currentPage == 'receipt') {
              const identifier = Date.now();
              receiptStore.add({
                id: identifier,
                paidValue: "",
                payer: "",
                payerAddress: "",
                referrerTo: ""
              });

              const receipt = receiptStore.receipts[identifier];
              return <Receipt
                key={receipt.id}
                index={receipt.id}
                payee={payeeStore.payee.name}
                payeeCpfOrRg={payeeStore.payee.cpfOrRg}
                payeeAddress={payeeStore.payee.address}
                date={payeeStore.payee.date}
                receipt={receipt}
              />
            }

            if (currentPage == 'receipts-to-print') {
              return <ReceiptsToPrint 
                receiptStore={receiptStore} 
                payeeStore={payeeStore}>
              </ReceiptsToPrint>
            }

            return "NOTFOUND"
          })()
        }
      </main>

      <footer className="bottom-app-bar">
        <a className="button  no-printable" href="#receipt-list">Recibos</a>
        <a className="button  no-printable" href="#receipts-to-print">Imprimir</a>
        <a className="button  no-printable" href="#receipt">Novo recibo</a>
        <a className="button  no-printable" href="#payee-form">Meus Dados</a>
      </footer>
    </div>
  );
});

export default App;
