import { makeObservable, makeAutoObservable, observable, computed, autorun } from "mobx"

class ReceiptStore {
  receipts = {};
  #receiptPersistence;
  
  constructor(receiptPersistence) {
    this.#receiptPersistence = receiptPersistence;
    this.receipts = this.#receiptPersistence.fetchData() || {};

    makeObservable(this, {
      receipts: observable
    });

    autorun(() => {
      console.log("---------beginreceipts----")
      for (const [key, value] of Object.entries(this.receipts)) {
        console.log(Object.assign({}, value))
      }
      console.log("--------------------------")

      this.#receiptPersistence.saveWithDelay(this.receipts);
    })
  }

  add(receipt) {
    if (!receipt.id) {
      throw new Error("You must pass receipt.id");
    }

    if (this.findById(receipt.id)) {
      throw new Error("This id is already in use");
    }

    this.receipts[receipt.id] = receipt;
    // this.receipts[receipt.id] = observable.object(receipt);
  }

  update(receipt) {
    if (!receipt.id) {
      throw new Error("You must pass receipt.id");
    }

    this.receipts[receipt.id] = receipt;
  }

  findById(id) {
    return this.receipts[id];
  }

  findAll() {
    return this.receipts;
  }

  removeById(id) {
    delete this.receipts[id];
  }

  removeAll() {
    throw new Error("Not implemented");
  }
}

export default ReceiptStore;