import React, { useState } from 'react';
import { observer } from "mobx-react";

import './ReceiptCard.css';

const ReceiptCard = observer((props) => {

    return (
      <div className="receipt-card">
        <h3 className="receipt-card__title">{props.receipt.payer || "..."}</h3>
        
        <div className="receipt-card__content">
          <p>{props.receipt.referrerTo || "..."}</p>
          <span className="receipt-card__price">R$ {props.receipt.paidValue || "?"}</span>
        </div>

        <div className="receipt-card__bottom">
          <div className="receipt-card__number">
            <span>Nº 121212</span>
          </div>

          <div className="receipt-card__separator"></div>

          <div className="receipt-card__action-bar">
            <a className="receipt-card__button" href={"#receipt/" + props.index}>
              <i class="fas fa-edit"></i>
            </a>
            <a className="receipt-card__button">
              <i class="fas fa-print"></i>
            </a>
            <a className="receipt-card__button" onClick={() => {
              const answer = window.confirm("Realmente deseja remover esse recibo?");
              if (answer == true) {
                props.removeReceipt(props.index)};
              }
            }>
              <i class="fas fa-trash"></i>
            </a>
          </div>
        </div>

      </div>
    );
  }
)
export default ReceiptCard;