import React, { useState } from 'react';
import { observer } from "mobx-react";

const PayeeForm = observer((props) => {
  const payeeStore = props.payeeStore;

  return (
    <div className="repeatable-fields-container no-printable">
      <div className="repeatable-fields">
        <label htmlFor="payee">Emitente</label>
        <input 
          className="u-full-width" 
          type="text" 
          name="payee" 
          placeholder="" 
          id="payee"
          value={payeeStore.payee.name} onChange={(e) => payeeStore.payee.name = e.target.value}/>
        
        <label htmlFor="payeeCpfOrRg">CPF/RG</label>
        <input 
          className="u-full-width" 
          type="text"   
          name="payeeCpfOrRg" 
          placeholder="" 
          id="payeeCpfOrRg"
          value={payeeStore.payee.cpfOrRg} onChange={(e) => payeeStore.payee.cpfOrRg = e.target.value}/>
        
        <label htmlFor="payeeAddress">Endereço</label>
        <input 
          className="u-full-width" 
          type="text"   
          name="payeeAddress" 
          placeholder="" 
          id="payeeAddress"
          value={payeeStore.payee.address} onChange={(e) => payeeStore.payee.address = e.target.value}/>
        <label htmlFor="date">Data</label>
        <input 
          className="u-full-width" 
          type="text"   
          name="date" 
          placeholder="" 
          id="date"
          value={payeeStore.payee.date} onChange={(e) => payeeStore.payee.date = e.target.value}/>
      </div>
  </div>
  );
})

export default PayeeForm;